.page-404 {
  @include flex(100%);
  padding: 92px 0 68px;
  .section-title {
    max-width: 360px;
  }
  &-row {
    @include flex-container();
  }
  &-col {
    @include flex(50%);
    &:last-child {
      margin-top: -80px;
    }
  }
  &-button {
    margin-top: 43px;
  }
  &-value {
    @include font(300px, 360px);
    font-weight: 200;
  }
}

@import "media";
