.header-form {
  position: fixed;
  z-index: 20;

  @include flex(100%);
  background: $grey-lighter;
  &--hidden {
    display: none;
  }
  &-row {
    @include flex-container(row, nowrap, center, space-between);
  }
  &-logo {
    @include flex(190px);
    text-decoration: none;
    &__stars {
      width: 46px;
      height: 10px;
      margin-bottom: -2px;

      background: url(../img/svg/stars.svg) no-repeat center;
      background-size: 100%;
    }
    &__name {
      width: 188px;
      height: 33px;
      margin-bottom: -6px;

      background: url(../img/svg/logo.svg) no-repeat center;
      background-size: 100%;
    }
    &__city {
      font-family: "Open Sans", "Arial", sans-serif;
      @include font(12px, 20px);
      letter-spacing: 0.3em;

      color: $gold;
    }
  }
  &__item {
    width: 894px;
    height: 80px;

    background: url(../img/temp/booking-dark.jpg) no-repeat top;
  }
}

@import "media";
