.about-gallery {
  @include flex(100%);
  padding: 56px 0;
  .mp-close {
    position: absolute;
    right: 50px;
    top: 52px;

    height: 14px;
    width: 14px;
    display: none;
    cursor: pointer;
    border: 0;
    outline: none;
    touch-action: manipulation;

    text-decoration: none;
    font-size: 24px;

    color: #fff;
  }
  .overlay {
    position: relative;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 20;

    background-color: $grey-darker;
    opacity: 0.95;
    cursor: default;
  }
  .overlay--active {
    position: fixed;
    & + .mp {
      position: fixed;
      top: 0;
      right: 0;
      left: 0;
      z-index: 20;

      padding: 0 0 50px;
      .mp-close {
        display: block;
      }
      .about-gallery-slider-thumbnails {
        display: flex;
      }
      .about-gallery__item {
        max-height: calc(100vh - 160px);
      }
      .about-gallery-slider__prev {
        top: 320px;
      }
    }
  }
  .mp--about-gallery {
    width: auto;
    max-width: 1200px;
    margin: 0 auto;
  }
  .tns-nav,
  .tns-outer button {
    display: none;
  }
  &-slider {
    overflow: hidden;
    &-controls {
      width: 900px;
      margin: 0 auto;
    }
    &-counter {
      display: none;
    }
    &__next,
    &__prev {
      position: relative;
      top: -290px;
      left: 943px;
      z-index: 15;

      width: 64px;
      height: 64px;
      border: 1px solid $gold;
      border-radius: 50%;

      outline: none;
      background: center url(../img/svg/arrow-gold.svg) no-repeat;
    }
    &__prev {
      top: 330px;
      left: -108px;

      transform: rotate(180deg);
    }
    &-thumbnails {
      @include flex-container(row, nowrap, normal, space-between);
      display: none;
      width: 900px;
      margin: -30px auto 0;
      padding: 0 5px;
      &__item {
        width: 50px;
        height: 50px;
        overflow: hidden;
        display: inline-block;
        padding: 4px;
        border: 1px solid transparent;

        cursor: pointer;
        &:last-child {
          margin-right: 0;
        }
        &:hover,
        &.tns-nav-active {
          border: 1px solid $gold;
        }
      }
      &__img {
        @include img();
      }
    }
  }
  &__item {
    max-width: 900px;
    width: 900px;
    max-height: 560px;
    height: 560px;
    overflow: hidden;
    margin: 0 auto;
  }
  &-image {
    width: 100%;
    height: 100%;
    overflow: hidden;
    margin: 0 auto;
  }
  &__img {
    @include img();
  }
}

@import "media";
