.transfer-price {
  @include flex(100%);
  padding: 80px 0 115px;
  &__title {
    margin-bottom: 32px;

    @include font(30px, 40px);
    font-weight: 300;

    color: $gold;
  }
  &-items {
    margin-bottom: 23px;
  }
  &__item {
    @include flex-container(row, nowrap, normal, space-between);
    padding: 14px 39px 20px;
    border: 1px solid $grey;
    &:last-child {
      border-top: none;
    }
  }
  &-col {
    &:last-child {
      @include flex-container(row, nowrap, center);
    }
  }
  &__name {
    margin-bottom: 5px;

    @include font(40px, 40px);
    font-weight: 300;
  }
  &__desc {
    color: $grey-lightest;
  }
  &-sum {
    margin-right: 60px;

    @include font(40px, 40px);
    font-weight: 300;
    &--mobile {
      display: none;
    }
  }
}

@import "media";
