.salon-price {
  @media only screen and (max-width: 1100px) {
    &-col {
      @include flex(50%);
      &:last-child {
        justify-content: flex-end;
      }
    }
    &-sum {
      margin-right: 40px;
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 43px 0 46px;
    &-list {
      margin: 26px 0 29px;
      overflow-x: scroll;
      &__item {
        min-width: max-content;
        padding: 0 20px 24px;
      }
    }
    &-items {
      margin-bottom: 14px;
    }
    &__item {
      padding: 0;
      &:hover {
        background: transparent;
        .salon-price {
          &-sum {
            margin-right: 0;
          }
          &-plus {
            display: block;
            margin-right: auto;
          }
        }
      }
    }
    &-col {
      @include flex(20%);
      min-height: 67px;
      @include flex-container(column, nowrap, normal, center);
      padding: 7px 0 12px;
      &:first-child {
        @include flex(80%);
        padding: 7px 0 12px 19px;
        border-right: 1px solid $grey;
      }
    }
    &__name {
      font-size: 20px;
      margin-bottom: -8px;
    }
    &-sum {
      display: none;
      &--mobile {
        display: block;
        margin: 0;

        @include font(20px, 40px);
      }
    }
    &-plus {
      display: block;
      margin: 0 auto;

      background: $gold url(../img/svg/add.svg) no-repeat center;
    }
    &-note {
      margin-bottom: 0;
    }
    &-button {
      display: none;
    }
  }
}
