.gallery {
  @media only screen and (max-width: 1100px) {
    &-row {
      flex-wrap: wrap;
    }
    &-slider-container,
    &-info {
      @include flex(750px);
    }
  }
  @media only screen and (max-width: 768px) {
    margin-bottom: 80px;
    .section-title--gallery {
      padding-top: 34px;
    }
    &-row {
      flex-direction: column;
      margin-top: 0;
    }
    &-info {
      @include flex(100%);
      order: -1;
      margin: 0;
      padding: 0;
      &:before {
        content: "";
        display: block;
        height: 1px;
        width: calc(100vw - 40px);
        margin: 0 auto;

        background: rgba($color: #fff, $alpha: 0.1);
      }
      &-text {
        margin-top: 10px;
      }
    }
    &-slider {
      &-container {
        position: relative;

        @include flex(100vw);
        max-height: 65vw;
        margin-left: -16px;
        margin-top: 36px;
      }
      &-image {
        max-height: 65vw;
        margin-top: 0;
      }
      &-controls {
        position: absolute;
        top: 15px;
        right: 17px;

        margin: 0;

        background: $grey-lighter-2;
        &-col {
          &:first-child {
            @include flex(auto);
          }
          &:last-child {
            display: none;
          }
        }
      }
      &__icon {
        display: none;
      }
      &-counter {
        margin: 0;
        padding: 5px 10px;

        @include font(10px, 10px);
      }
    }
  }
}
