.footer {
  @media only screen and (max-width: 1000px) {
    &-nav__item {
      @include flex(50%);
    }
    &-contacts {
      @include flex(100%);
    }
  }
  @media only screen and (max-width: 768px) {
    padding-top: 25px;
    &-top {
      display: none;
      &--mobile {
        display: block;
      }
    }
    &-nav {
      flex-direction: column;
      &:first-child {
        &:after {
          display: none;
        }
      }
      &__item {
        @include flex(100%);
      }
      &__title {
        position: relative;

        display: block;
        margin: 0;
        padding: 12px 0 17px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);

        line-height: 20px;
        font-weight: 300;
      }
      &__icon {
        position: absolute;
        top: 13px;
        right: 0;

        display: inline-block;
        width: 24px;
        height: 24px;

        background: url(../img/svg/arrow-more.svg) no-repeat center;
        &--shown {
          transform: rotate(180deg);
        }
      }
      &-list {
        display: none;
        margin-top: 15px;
        &--shown {
          display: block;
        }
      }
    }
    &-social {
      padding: 25px 0 30px;
      &-links {
        .footer-social__link {
          width: 50px;
          height: 50px;
          margin-right: 35px;
          border-radius: 50%;

          background: $gold url(../img/svg/wa-white.svg) no-repeat center;
          &--facebook {
            background-image: url(../img/svg/facebook-white.svg);
          }
          &--inst {
            background-image: url(../img/svg/inst-white.svg);
          }
        }
      }
      &-feedback {
        text-decoration: none;
        outline: none;
        &__logo {
          width: 151px;
          height: 25px;
          margin: 35px 0 5px;

          background: url(../img/svg/booking-com.svg) no-repeat center;
        }
        &-content {
          @include flex-container();
          color: #fff;
          &-mark {
            display: flex;
            width: 35px;
            height: 35px;
            margin-right: 10px;
            padding: 8px 7px 7px;
            border-radius: 6px 6px 6px 0px;

            font-weight: 800;

            background: #2192cd;
          }
          &__num {
            margin-top: -4px;
            font-size: 12px;

            color: $grey;
          }
        }
      }
    }
    &-bottom {
      justify-content: flex-start;
      width: 100vw;
      margin-left: -16px;
      padding: 20px 16px;

      background-color: #171717;
    }
  }
}
