.about {
  @include flex(100%);
  padding: 67px 0 120px;
  &-text {
    @include flex(74%);

    @include font(40px, 40px);
    font-weight: 300;
    &-row {
      @include flex-container(row, nowrap, center, flex-end);
      margin-bottom: 118px;
    }
  }
  &-video {
    position: relative;

    width: 100vw;
    height: 500px;
    margin-bottom: 127px;
    &:before {
      content: "";
      position: absolute;

      width: 110vw;
      height: 100%;
      background: rgba(34, 35, 36, 0.4);
    }
    &:after {
      content: "";
      position: absolute;
      top: calc(50% - 40px);
      left: calc(50% - 40px);

      width: 80px;
      height: 80px;
      border-radius: 50%;

      background: rgba(255, 255, 255, 0.3) url(../img/svg/play.svg) no-repeat
        center;
    }
    img {
      max-width: 150vw;
      @include img();
    }
  }
  &-info {
    @include flex-container(row, nowrap, normal, space-between);
    &-col {
      @include flex(37.5%);
      &:last-child {
        @include flex(60%);
        @include flex-container();
      }
    }
    &__desc {
      margin: 17px 0 40px;
    }
    &-image {
      position: relative;
      left: 25px;

      height: 380px;
      &:first-child {
        top: 160px;
        left: 78px;
        z-index: 5;

        height: 280px;
      }
      &:before {
        content: "";
        position: absolute;

        width: 100%;
        height: 100%;
        background: rgba(161, 143, 87, 0.1);
      }
    }
  }
}

@import "media";
