.room {
  @include flex(100%);
  margin-bottom: 112px;
  padding-top: 67px;
  &-row {
    @include flex-container();
  }
  &-col {
    @include flex(50%);
  }
  &-features {
    @include flex-container(row, wrap, center, space-between);
    margin: 45px 0 40px;
    max-width: 450px;
    &--mobile {
      display: none;
    }
  }
  &-feature {
    @include flex(33%);
    display: flex;
    margin-bottom: 30px;
    &__icon {
      display: inline-block;
      width: 20px;
      height: 20px;
      margin-right: 10px;

      background: url(../img/svg/area.svg) no-repeat center;
      &--bed {
        background-image: url(../img/svg/bed.svg);
      }
      &--bath {
        background-image: url(../img/svg/bath.svg);
      }
      &--table {
        background-image: url(../img/svg/table.svg);
      }
      &--drink {
        background-image: url(../img/svg/drink.svg);
      }
      &--armchair {
        background-image: url(../img/svg/armchair.svg);
      }
    }
  }
  &__button {
    @include flex-container(row, nowrap, center);
  }
  &__price {
    margin-left: 40px;

    @include font(30px, 40px);
  }
  &__desc {
    margin: 22px 0 45px;
    max-width: 450px;
  }
  .tns {
    &-nav + button,
    &-controls {
      display: none;
    }
    &-nav {
      & > button {
        display: none;
      }
    }
  }
  &-slider {
    &-container {
      width: 100%;
      margin-bottom: 115px;
    }
    &-image {
      max-width: 100%;
      max-height: 450px;
      overflow: hidden;
      margin: 0 auto;
    }
    &__img {
      @include img();
    }
    &-controls {
      margin-top: 10px;
      @include flex-container(row, nowrap, normal, space-between);
      &-content {
        @include flex(130px);
        @include flex-container(row, nowrap, center, space-between);
        margin-top: 15px;
      }
    }
    &__full,
    &__next,
    &__prev {
      width: 18px;
      height: 12px;

      cursor: pointer;
      outline: none;
      background: url(../img/svg/arrow.svg) no-repeat center;
    }
    &__prev {
      transform: rotate(180deg);
    }
    &__full {
      width: 14px;
      height: 14px;

      background-image: url(../img/svg/fullscreen.svg);
    }
  }
  &-info {
    &-titles {
      @include flex-container($justify-content: space-around);
      margin-bottom: 42px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    }
    &__title {
      padding: 0 0 22px;

      @include font(15px, 30px);
      text-decoration: none;

      color: #fff;
      &--active {
        border-bottom: 2px solid $gold;

        color: $gold;
      }
    }
    &-preview {
      display: none;
      &--active {
        display: block;
      }
      &__title {
        display: none;
      }
      &__item {
        @include flex-container();
        &:before {
          content: "";
          display: inline-block;
          min-width: 24px;
          height: 30px;
          margin-right: 10px;

          background: url(../img/svg/check.svg) no-repeat center;
        }
      }
      &__name {
        @include font(15px, 35px);
      }
    }
  }
}

@import "media";
