.other-rooms {
  @media only screen and (max-width: 950px) {
    &-preview {
      &-info {
        @include flex(45%);
      }
      &-slider {
        @include flex(55%);
        margin-top: 30px;
      }
      &-features {
        margin-bottom: 60px;
      }
      &-feature {
        @include flex(50%);
      }
      &__button {
        flex-direction: column;
        align-items: normal;
        .btn {
          order: 1;
          max-width: 240px;
        }
      }
      &__price {
        margin-left: 0;
        margin-bottom: 20px;
      }
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 43px 0 61px;
    .section-title--other-rooms {
      display: block;
    }
    &-row {
      flex-direction: column;
    }
    &-list {
      order: 1;
      flex-direction: row;
      flex-basis: auto;
      justify-content: space-between;
      margin: 26px 0 0;
      border: none;
      &__item {
        @include flex(12%);
        margin: 0;
        margin-right: 8px;
        padding: 0;
        border-bottom: 2px solid rgba(255, 255, 255, 0.1);
        &--active {
          border-left: none;
          border-bottom: 2px solid #a18f57;
        }
        &:last-child {
          margin-right: 0;
        }
      }
    }
    &__title,
    &__price {
      display: none;
    }
    &-preview {
      &-list {
        @include flex(100%);
        @include flex-container();
        overflow-x: scroll;
        &__item {
          display: block;
          @include flex(83%);
          padding-top: 32px;
          margin-right: 36px;
          &-inner {
            flex-direction: column;
          }
        }
      }
      &-info {
        @include flex(100%);
        order: 1;
        margin-top: 10px;
        &-detail {
          display: flex;
          justify-content: center;

          text-decoration: underline;

          color: $gold;
          &--active {
            display: none;
            & + .other-rooms-preview-info-inner {
              display: block;
            }
          }
        }
        &-inner {
          display: none;
        }
        &--mobile {
          display: block;
          .other-rooms-preview__button {
            display: block;
            margin: 26px 0 29px;
          }
          .other-rooms-preview__title {
            display: block;
            margin-bottom: 7px;

            @include font(20px, 20px);
            text-decoration: none;
            font-weight: 400;

            color: $gold;
          }
          .other-rooms-preview__price {
            display: block;
            margin-left: 0;

            @include font(15px, 20px);

            color: $grey-lightest;
            &--big {
              @include font(15px, 20px);
            }
          }
        }
        /*&--hidden {
          display: none;
        }*/
      }
      &__button,
      &__title,
      &__price {
        display: none;
      }
      &__button {
        .btn {
          max-width: 100%;
        }
      }
      &-features {
        margin-bottom: 0;
      }
      &-feature {
        @include flex(33%);
      }
      &-slider {
        position: relative;

        @include flex(100%);
        width: 100%;
        max-height: 50vw;
        margin: 0;
        &-image {
          max-height: 50vw;
        }
        &-controls {
          display: none;
        }
      }
    }
  }
  @media only screen and (max-width: 600px) {
    &-preview-feature {
      @include flex(50%);
    }
  }
}
