.transfer {
  @include flex(100%);
  padding: 67px 0 120px;
  &-row {
    @include flex-container();
  }
  &-back {
    width: 100px;
    height: 60px;
    border: 1px solid $gold;
    margin-right: 200px;

    background: url(../img/svg/arrow-gold.svg) no-repeat center;

    transform: rotate(180deg);
  }
  &-content {
    @include flex(550px);
    &-text {
      margin: 17px 0 20px;
    }
    &-features {
      margin-bottom: 30px;

      color: $grey-lightest;
      &__item {
        @include flex-container();
        margin-top: 15px;
        &:before {
          content: "";
          display: inline-block;
          width: 18px;
          height: 14px;
          margin-right: 10px;

          background: url(../img/svg/check.svg) no-repeat center;
        }
      }
    }
  }
}

@import "media";
