.extra-services {
  @include flex(100%);
  padding: 126px 0 119px;
  .section-title--extra-services {
    margin-bottom: 18px;
  }
  &__text {
    margin-bottom: 45px;
  }
  &-row {
    @include flex-container(row, wrap, flex-start, space-between);
    margin-bottom: 86px;
  }
  &__item {
    @include flex(30%);
    max-width: 300px;
  }
  &-image {
    max-width: 100%;
    max-height: 150px;
    height: 150px;
    overflow: hidden;
    margin: 0 auto;
  }
  &__img {
    @include img();
    max-height: 150px;
  }
  &__title {
    margin-top: 17px;

    @include font(25px, 30px);
  }
  &-more {
    width: 100%;
    display: flex;
    justify-content: center;
    &__text {
      border-bottom: 1px dashed $gold;

      text-transform: uppercase;

      color: $gold;
    }
    &__icon {
      width: 12px;
      height: 8px;
      display: inline-block;
      margin-left: 14px;

      background: url(../img/svg/more-gold.svg) no-repeat center;
    }
  }
}
@import "media";
