.header {
  position: fixed;
  //top: -20px;
  z-index: 20;

  display: block;
  @include flex(100%);
  background: $grey-lighter;
  &--hidden {
    display: none;
  }
  &--small {
    height: 80px;
    .header-row {
      padding: 3px 0 0;
    }
    .header-nav-list__item {
      padding: 30px 0;
      &--menu {
        padding: 30px 5px;
      }
    }
  }
  &-row {
    @include flex-container(row, nowrap, center, space-between);
    padding: 15px 0 0;
  }
  &-logo {
    @include flex(190px);
    text-decoration: none;
    &__stars {
      width: 46px;
      height: 10px;
      margin-bottom: -2px;

      background: url(../img/svg/stars.svg) no-repeat center;
      background-size: 100%;
    }
    &__name {
      width: 188px;
      height: 33px;
      margin-bottom: -6px;

      background: url(../img/svg/logo.svg) no-repeat center;
      background-size: 100%;
    }
    &__city {
      font-family: "Open Sans", "Arial", sans-serif;
      @include font(12px, 20px);
      letter-spacing: 0.3em;

      color: $gold;
    }
  }
  &-nav {
    @include flex(53%);
    margin-top: -5px;
    &-icon {
      display: none;
      width: 24px;
      height: 24px;
      overflow: hidden;

      background: url(../img/svg/menu.svg) no-repeat center;
    }
    &-list {
      @include flex-container(row, nowrap, center, space-between);
      &--mobile {
        display: none;
      }
      &__item {
        padding: 40px 0;
        &--menu {
          padding: 40px 5px;
          .header-nav__link {
            display: block;
            width: 20px;
            height: 15px;
            margin-top: -3px;

            background: url(../img/svg/menu.svg) no-repeat center;
          }
        }
        &--active-menu {
          .header-nav__link {
            background-image: url(../img/svg/close.svg);
          }
          .header-subnav-container {
            display: block;
          }
        }
      }
    }
    &__link {
      @include font(15px, 20px);
      text-transform: uppercase;
      text-decoration: none;

      color: #fff;
      &:hover {
        color: $gold;
      }
    }
  }
  &-contacts {
    @include flex(175px);

    text-align: right;
    &-links {
      &--mobile {
        display: none;
      }
    }
    &__value {
      @include font(20px, 20px);
      text-decoration: none;

      color: #fff;
      &:hover {
        color: $gold;
      }
    }
    &__link {
      @include font(14px, 15px);

      color: $gold;
      &:hover {
        color: #fff;
      }
    }
    &__button {
      display: none;
      .btn {
        padding: 10px 12px;
        width: auto;

        font-size: 10px;
      }
    }
  }
  &-subnav {
    position: relative;

    width: 1200px;
    //max-width: calc(100% - 190px);
    @include flex-container(row, nowrap, normal, space-around);
    &:before {
      content: "";
      display: block;
      min-width: 105px;
    }
    &-social {
      position: absolute;
      top: 100px;
      left: 0;
      z-index: 10;

      width: 20px;
      height: 100px;
      &-links {
        @include flex-container(column, nowrap, center);
        .header-subnav-social__link {
          width: 20px;
          height: 20px;
          margin-bottom: 20px;

          background: url(../img/svg/wa.svg) no-repeat center;
          &--facebook {
            width: 10px;

            background-image: url(../img/svg/facebook.svg);
          }
          &--inst {
            background-image: url(../img/svg/inst.svg);
          }
        }
      }
    }
    &-container {
      position: absolute;
      top: 105px;
      left: 0;
      z-index: 10;

      display: none;
      width: 100vw;
      padding: 45px 0 125px;
      box-shadow: 0px 4px 40px #0f0f0f;

      background: $grey-darker;
    }
    &-row {
      max-width: 1200px;
      margin: 0 auto;
      display: flex;
    }
    &-list {
      @include flex(21.25%);
    }
    &-list__item,
    &-rooms__title,
    &-login__title {
      display: block;
      margin-bottom: 15px;

      @include font(30px, 40px);
      font-weight: 300;
      text-decoration: none;

      color: #fff;
      &:hover {
        color: $gold;
      }
    }
    &-rooms {
      @include flex(170px);
      &__item {
        display: block;
        margin-bottom: 15px;

        text-decoration: none;

        color: $grey;
        &:hover {
          color: $gold;
        }
      }
    }
    &-login {
      @include flex(170px);
      &__link {
        color: $gold;
        &:hover {
          color: #fff;
        }
      }
    }
  }
}

@import "media";
