@media only screen and (max-width: 950px) {
  .body {
    .main {
      margin-top: 70px;
    }
  }
}
@media only screen and (max-width: 768px) {
  .container {
    padding: 0 16px;
    max-width: 100%;
  }
  .btn {
    width: 100%;
  }
  .section-title {
    @include font(30px, 40px);
  }
}
