.hotel-services {
  @media only screen and (max-width: 1280px) {
    &-row {
      margin: 0 -75px;
    }
  }
  @media only screen and (max-width: 1100px) {
    &-row {
      margin: 0 -50px;
    }
    &__item {
      padding: 0 50px;
    }
  }
  @media only screen and (max-width: 900px) {
    &-row {
      justify-content: space-between;
      margin: 0 -25px;
    }
    &__item {
      padding: 0 25px;
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 32px 0 60px;
    &-row {
      margin: 0;
    }
    &__text {
      max-width: 100%;
      margin: 11px 0 21px;
    }
    &__item {
      @include flex(45%);
      margin-bottom: 50px;
      padding: 0;
    }
    /*&__title {
      margin-top: 15px;
      @include font(20px, 25px);
    }*/
  }
  @media only screen and (max-width: 550px) {
    &-row {
      margin: 0;
    }
    &__item {
      @include flex(100%);
      margin-bottom: 50px;
      padding: 0;
    }
    &__title {
      margin-top: 15px;
      @include font(20px, 25px);
    }
    &__img {
      max-height: 200px;
    }
  }
}
