.conference-booking {
  @media only screen and (max-width: 768px) {
    padding: 43px 0 60px;
    .section-title--gold {
      display: inline;
    }
    &-row {
      flex-wrap: wrap;
    }
    &-col {
      @include flex(100%);
      &:last-child {
        padding: 0;
      }
    }
    &-text {
      max-width: 100%;
      margin-bottom: 10px;
    }
    &-note {
      max-width: 100%;
      margin-bottom: 40px;
    }
  }
}
