.conference-info {
  @include flex(100%);
  padding: 120px 0;
  &-row {
    @include flex-container($justify-content: space-between);
  }
  &-col {
    @include flex(50%);
    &:last-child {
      max-width: 420px;
      margin-top: 9px;
    }
  }
  &-text {
    @include font(40px, 40px);
    font-weight: 300;
    &--gold {
      color: $gold;
    }
  }
  &-list {
    &__item {
      @include flex-container(row, nowrap, center);
      margin-top: 15px;
      &:before {
        content: "";
        display: inline-block;
        width: 18px;
        height: 14px;
        margin-right: 10px;
        background: url(../img/svg/check.svg) no-repeat center;
      }
    }
  }
}

@import "media";
