.room-bottom {
  @media only screen and (max-width: 768px) {
    position: relative;

    display: block;
    height: 70px;
    @include flex(100%);
    &-row {
      position: fixed;
      bottom: 0;

      width: 100vw;
      @include flex-container();
      height: 70px;
    }
    &__price {
      @include flex(50%);
      padding: 20px 16px;

      @include font(20px, 30px);
      font-weight: 300;

      background: $grey-lighter;
    }
    &__button {
      @include flex(50%);
      .btn {
        padding: 25px 40px;
      }
    }
  }
}
