.salon {
  @include flex(100%);
  padding: 67px 0 120px;
  &-row {
    @include flex-container();
  }
  &-back {
    width: 100px;
    height: 60px;
    border: 1px solid $gold;
    margin-right: 200px;

    background: url(../img/svg/arrow-gold.svg) no-repeat center;

    transform: rotate(180deg);
  }
  &-text {
    margin: 17px 0 20px;
  }
  &-content {
    @include flex(550px);
  }
  &-gallery {
    &-row {
      @include flex-container(row, nowrap, normal, space-between);
      margin-top: 36px;
      .tns {
        &-nav + button,
        &-controls {
          display: none;
        }
        &-nav {
          & > button {
            display: none;
          }
        }
      }
    }
    &-slider {
      &-row {
        margin: 0 -15px;
        @include flex-container(row, wrap, normal);
        max-width: 780px;
        transform: none !important;
        .salon-gallery-slider-image {
          @include flex(156px);
          max-height: 79px;
          padding: 0 15px;
        }
        .salon-gallery-slider__img {
          opacity: 0.8;
        }
        .salon-gallery-slider-container {
          padding: 0 15px;
        }
      }
      &-container {
        @include flex(750px);
      }
      &-image {
        max-width: 750px;
        max-height: 405px;
        margin-top: 30px;
        overflow: hidden;
      }
      &__img {
        @include img();
      }
      &-controls {
        margin: 6px 0 0;
        @include flex-container(row, nowrap, normal, space-between);
        &-col {
          @include flex-container(row, nowrap, center);
          &:first-child {
            @include flex(150px);
          }
          &:last-child {
            @include flex(150px);
          }
        }
      }
      &-counter {
        margin-right: 20px;
      }
      &__icon,
      &__next,
      &__prev,
      &__full {
        width: 48px;
        height: 48px;
        border-radius: 50%;

        cursor: pointer;
        outline: none;
        background: url(../img/svg/arrow.svg) no-repeat center;
        &:hover {
          background-color: rgba(255, 255, 255, 0.1);
        }
      }
      &__prev {
        transform: rotate(180deg);
      }
      &__full {
        background: url(../img/svg/fullscreen.svg) no-repeat center;
      }
      &__icon {
        background-image: url(../img/svg/carousel.svg);
        &--active {
          background-image: url(../img/svg/carousel-gold.svg);
          &.salon-gallery-slider__icon--module {
            background-image: url(../img/svg/module-gold.svg);
          }
        }
        &--module {
          background-image: url(../img/svg/module.svg);
        }
      }
    }
    &-info {
      @include flex(28.5%);
      padding-top: 52px;
      margin-right: 40px;
      &-text {
        margin-top: 22px;
      }
    }
  }
}

@import "media";
