.welcome {
  &-content {
    &:before {
      @media only screen and (max-width: 1280px) {
        left: calc(100vw - 1000px);
      }
      @media only screen and (max-width: 850px) {
        left: calc(100vw - 850px);
      }
    }
  }
  @media only screen and (max-width: 768px) {
    &-social {
      display: none;
    }
    &-content {
      left: -16px;
      margin-top: 150px;
      min-height: 300px;
      width: 100vw;
      background: #fff url(../img/temp/restaurants.png) no-repeat center;
      background-size: cover;
      &:before {
        top: 0;
        left: 0;
        height: 100%;
        background: linear-gradient(
          100.45deg,
          rgba(34, 35, 36, 0.6) 13.02%,
          rgba(34, 35, 36, 0.3) 67.71%,
          rgba(34, 35, 36, 0.18) 98.64%
        );
      }
      &-inner {
        top: -150px;
        left: 16px;
        height: 116px;
        margin-top: 34px;
      }
      &-slogan {
        margin-bottom: 10px;
      }
      &-logo {
        margin-bottom: 6px;
        @include font(30px, 30px);
      }
      &-city {
        @include font(22px, 22px);
      }
    }
    &-booking {
      width: 100vw;
      height: 80px;
      margin: 0;
      margin-left: -16px;
      background: #fff url(../img/svg/booking-mob.svg) no-repeat 0 20%;
      background-size: cover;
      img {
        display: none;
      }
    }
    &__button {
      display: block;
      margin-top: 20px;
      margin-bottom: 50px;
    }
    &-down {
      display: none;
    }
  }
  @media only screen and (max-width: 480px) {
    &-content {
      min-height: 210px;
    }
    &-booking {
      height: 60px;
    }
  }
}
