.other-rooms {
  @include flex(100%);
  padding: 125px 0;

  //background: $grey-darker;
  &-row {
    @include flex-container(column);
  }
  &-list {
    @include flex-container($justify-content: space-around);
    margin-top: 53px;
    margin-bottom: 19px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    &__item {
      display: block;
      padding-bottom: 19px;

      text-decoration: none;
      &--active {
        border-bottom: 2px solid $gold;
        .rooms__title {
          color: $gold;
        }
      }
    }
  }
  &__title {
    color: #fff;
  }
  &__price {
    @include flex-container();

    color: $grey-lightest;
    &--star {
      &:after {
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        margin-top: -2px;
        margin-left: 4px;

        background: url(../img/svg/star.svg) no-repeat, center;
      }
    }
  }
  &-preview {
    &-list {
      max-width: 1070px;
      &__item {
        display: none;
        &--active {
          display: block;
        }
        &-inner {
          @include flex-container($justify-content: space-between);
        }
      }
    }
    &-info {
      margin-top: 47px;
      @include flex(450px);
      &--mobile,
      &-detail {
        display: none;
      }
    }
    &__title {
      display: block;
      margin-bottom: 30px;

      @include font(40px, 40px);
      font-weight: 300;
      text-decoration: 1px #fff solid;
      text-decoration-line: underline;
      text-decoration-style: solid;
      text-decoration-color: #fff;
      text-decoration-thickness: 1px;

      color: #fff;
    }
    &-features {
      @include flex-container(row, wrap, center, flex-start);
      align-items: center;
      margin-bottom: 110px;
    }
    &-feature {
      @include flex(32%);
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      &__icon {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-right: 10px;

        background: url(../img/svg/area.svg) no-repeat center;
        &--bed {
          background-image: url(../img/svg/bed.svg);
        }
        &--bath {
          background-image: url(../img/svg/bath.svg);
        }
        &--table {
          background-image: url(../img/svg/table.svg);
        }
        &--drink {
          background-image: url(../img/svg/drink.svg);
        }
        &--armchair {
          background-image: url(../img/svg/armchair.svg);
        }
      }
      &__text {
        line-height: 15px;
      }
    }
    &__button {
      @include flex-container(row, nowrap, center);
    }
    &__price {
      @include font(14px, 15px);
      margin-left: 40px;
      &--big {
        @include font(25px, 40px);
      }
    }
    &-slider {
      @include flex(450px);
      .tns {
        &-nav + button,
        &-controls {
          display: none;
        }
        &-nav {
          & > button {
            display: none;
          }
        }
      }
      &-image {
        max-width: 100%;
        max-height: 450px;
        overflow: hidden;
        margin: 0 auto;
      }
      &__img {
        @include img();
      }
      &-controls {
        margin-top: 6px;
        @include flex-container(row, nowrap, normal, space-between);
        &-content {
          @include flex(150px);
          @include flex-container(row, nowrap, center, space-between);
        }
      }
      &-counter {
        margin-top: 13px;
      }
      &__full,
      &__next,
      &__prev {
        width: 48px;
        height: 48px;
        border-radius: 50%;

        cursor: pointer;
        outline: none;
        background: url(../img/svg/arrow.svg) no-repeat center;
        &:hover {
          background-color: rgba(255, 255, 255, 0.1);
        }
      }
      &__prev {
        transform: rotate(180deg);
      }
      &__full {
        background: url(../img/svg/fullscreen.svg) no-repeat center;
      }
    }
  }
}

@import "media";
