.book {
  @media only screen and (max-width: 768px) {
    padding: 43px 0 60px;
    .section-title--book {
      margin: 0;
    }
    &-room {
      margin-bottom: 40px;
    }
    &-form {
      width: 100vw;
      min-height: 60px;
      margin: -30px auto -75px;
      margin-left: -16px;

      background: url(../img/svg/booking-mob.svg) no-repeat center;
      background-size: contain;
    }
    &__button {
      display: block;
    }
  }
  @media only screen and (max-width: 350px) {
    &-room__link {
      max-width: 200px;

      text-align: center;
    }
  }
}
