.opinion {
  @include flex(100%);
  padding: 125px 0;
  .section-title--gold {
    color: $gold;
  }
  &-row {
    @include flex-container(row, nowrap, normal, space-between);
  }
  &-col {
    @include flex(450px);
  }
  .form {
    &--opinion {
      margin-top: 3px;
    }
  }
  &__desc {
    margin-top: 18px;
  }
  &__button {
    display: none;
  }
}

@import "media";
