.extra-services {
  @media only screen and (max-width: 768px) {
    padding: 43px 0 60px;
    .section-title--extra-services {
      margin-bottom: 11px;
    }
    &__text {
      margin-bottom: 26px;
    }
    &-row {
      margin-bottom: 0;
      flex-wrap: nowrap;
      align-items: flex-start;
      overflow-x: scroll;
    }
    &__item {
      margin-right: 35px;
      flex: auto;
      min-width: 244px;
    }
    &-image {
      width: 244px;
      height: 120px;
      max-height: 120px;
      margin: 0;
    }
    &__img {
      max-height: 120px;
    }
    &__title {
      margin-top: 8px;

      @include font(20px, 25px);
    }
    &-more {
      display: none;
    }
  }
