.page-404 {
  @media only screen and (max-width: 1000px) {
    padding: 20px 0 40px;
    .section-title {
      max-width: 100%;
    }
    &-row {
      flex-wrap: wrap;
    }
    &-col {
      @include flex(100%);
      &:last-child {
        margin-top: 0;
      }
    }
  }
  @media only screen and (max-width: 550px) {
    &-value {
      @include font(52vw, 60vw);
    }
  }
}
