.features {
  @media only screen and (max-width: 1000px) {
    margin-top: 43px;
    .section-title--features {
      display: block;
    }
    &-list {
      flex-wrap: wrap;
      margin: 30px 0 10px;
      padding: 0;
      border: none;
      &__item {
        @include flex(50%);
        align-items: center;
        margin-bottom: 30px;
      }
    }
    &__icon {
      min-width: 30px;
      width: 30px;
      height: 30px;
      margin-right: 13px;

      background-size: 85%;
    }
  }
}
