.fitness {
  @media only screen and (max-width: 1100px) {
    &-row {
      justify-content: space-between;
    }
    &-back {
      margin: 0;
    }
    &-content {
      @include flex(75%);
    }
    &-gallery-slider-container {
      @include flex(100%);
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 33px 0 60px;
    &-row {
      flex-wrap: wrap;
    }
    &-back {
      display: none;
    }
    &-content {
      @include flex(100%);
    }
    &-text {
      margin: 11px 0 36px;
    }
    &-gallery {
      &-row {
        margin-top: 0;
        margin-left: -16px;
        width: 100vw;
      }
      &-slider {
        &-container {
          position: relative;

          @include flex(100vw);
          max-height: 65vw;
        }
        &-image {
          max-height: 65vw;
        }
        &-controls {
          position: absolute;
          top: 15px;
          right: 17px;

          margin: 0;

          background: $grey-lighter-2;
          &-col {
            &:first-child {
              @include flex(auto);
            }
            &:last-child {
              display: none;
            }
          }
        }
        &__icon {
          display: none;
        }
        &-counter {
          margin: 0;
          padding: 5px 10px;

          @include font(10px, 10px);
        }
        &-image {
          max-width: 100%;
          height: auto;
          margin-top: 0;
        }
      }
    }
  }
}
