.offers {
  @media only screen and (max-width: 1279px) {
    &-slider {
      &-container {
        margin-left: 40px;
        max-width: 100vw;
      }
    }
  }
  @media only screen and (max-width: 800px) {
    &-slider {
      transform: none !important;
      overflow-x: scroll;
      &-container {
        margin-left: 16px;
      }
      &__item {
        max-width: 335px;
        width: calc(68vw + 35px) !important;
        &:hover .offers-slider-image {
          &:before {
            max-width: 300px;
            width: 68vw;
            max-height: 150px;
            height: 33vw;
          }
        }
      }
      &-image {
        max-width: 300px;
        width: 68vw;
        max-height: 150px;
        height: 33vw;
        margin: 0;
        margin-bottom: 10px;
      }
      &__title {
        margin-bottom: 0;
        max-width: 300px;
        width: 68vw;

        @include font(20px, 25px);
      }
      &__price,
      &__text {
        display: none;
      }
      &-controls {
        display: none;
      }
    }
  }
}
