.opinion {
  @media only screen and (max-width: 768px) {
    padding: 48px 0 60px;
    .section-title {
      line-height: 30px;
      &--gold {
        display: block;
      }
    }
    &-col {
      @include flex(100%);
      &:last-child {
        display: none;
      }
    }
    &__desc {
      margin: 16px 0 26px;
    }
    &__button {
      display: block;
    }
  }
}
