.welcome {
  @include flex(100%);
  &-inner {
    position: relative;
  }
  &-social {
    position: absolute;
    top: 140px;
    left: 0;
    z-index: 10;

    width: 20px;
    height: 100px;
    &-links {
      @include flex-container(column, nowrap, center);
      .welcome-social__link {
        width: 20px;
        height: 20px;
        margin-bottom: 20px;

        background: url(../img/svg/wa.svg) no-repeat center;
        &--facebook {
          width: 10px;

          background-image: url(../img/svg/facebook.svg);
        }
        &--inst {
          background-image: url(../img/svg/inst.svg);
        }
      }
    }
  }
  &-content {
    position: relative;
    z-index: 5;

    min-height: 380px;
    width: 100%;

    background: url(../img/temp/welcome1.png) no-repeat right;
    &:before {
      content: "";
      position: absolute;
      left: 295px;
      top: -5px;

      width: 100%;
      height: 102%;
      background: linear-gradient(
        101.09deg,
        #222324 12.87%,
        rgba(34, 35, 36, 0.37) 57%,
        rgba(34, 35, 36, 0) 79.39%
      );
    }
    &-inner {
      position: absolute;
      top: 90px;
      left: 155px;

      width: 100%;
      height: 200px;
    }
    &-slogan {
      margin-bottom: 5px;

      color: $gold;
    }
    &-logo {
      margin-bottom: -14px;

      @include font(75px, 82px);
      text-transform: uppercase;
    }
    &-city {
      @include font(60px, 90px);
      font-weight: 300;
      letter-spacing: 0.15em;
      text-transform: uppercase;
    }
  }
  &-booking {
    display: flex;
    justify-content: flex-end;
    margin: 4px -10px -20px 0;
  }
  &__button {
    display: none;
  }
  &-down {
    position: relative;
    bottom: 112px;

    width: 32px;
    height: 32px;

    background: url(../img/svg/arrow-big.svg) no-repeat center;
  }
}

@import "media";
