.about-gallery {
  @media only screen and (max-width: 1100px) {
    &-slider {
      &-controls {
        max-width: 85%;
      }
      &__prev {
        left: -9vw;
      }
      &__next {
        left: calc(100vw - 190px);
      }
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 0 0 80px;
    &-slider {
      &-controls {
        position: relative;

        width: 100vw;
        max-width: 100vw;
        margin: 0 0 0 -16px;
      }
      &-counter {
        position: absolute;
        top: 15px;
        right: 17px;
        z-index: 10;

        display: block;
        margin: 0;
        padding: 5px 10px;

        @include font(10px, 10px);

        background: $grey-lighter-2;
      }
      &__prev,
      &__next {
        display: none;
      }
    }
    &__item {
      max-height: 65vw;
    }
    /*&-image,
    &__img {
      max-height: 50vw;
    }*/
  }
}
