.header {
  @media only screen and (max-width: 1280px) {
    &-subnav-social {
      left: 45px;
    }
  }
  @media only screen and (max-width: 1100px) {
    &-nav {
      @include flex(57%);
    }
  }
  @media only screen and (max-width: 950px) {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    &--small,
    &-form {
      display: none;
    }
    &-row {
      height: 70px;
      padding: 0;
      &--mobile {
        .header-nav-icon--close {
          background-image: url(../img/svg/close.svg);
        }
        .header-nav-list--mobile {
          display: block;
          margin: 0;
          max-height: calc(100vh - 70px);
          overflow-y: scroll;
        }
        .header-contacts {
          &__button {
            display: none;
          }
          &-links--mobile {
            @include flex-container();
          }
        }
      }
    }
    &-logo {
      @include flex(150px);
      &__stars {
        margin-bottom: 2px;
      }
      &__name {
        width: 150px;
        height: 20px;
        margin-bottom: -2px;
      }
      &__city {
        font-size: 10px;
      }
    }
    &-nav {
      order: -1;
      @include flex(30px);
      &-list {
        display: none;
        &--mobile {
          position: absolute;
          left: 0;
          top: 71px;

          width: 100vw;
          display: none;
          padding: 20px 16px 0;

          background: $grey-lighter;
        }
        &__item {
          padding: 5px 0;
          &--menu {
            display: none;
          }
        }
      }
      &-icon {
        display: block;
      }
      &__title {
        position: relative;

        display: block;
        margin: 0;
        padding: 12px 0 17px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);

        @include font(20px, 20px);
        font-weight: 300;
        text-decoration: none;

        color: #fff;
      }
      &-more {
        position: absolute;
        top: 13px;
        right: 0;

        display: inline-block;
        width: 24px;
        height: 24px;
        background: url(../img/svg/arrow-more.svg) no-repeat center;
        &--shown {
          transform: rotate(180deg);
        }
      }
      &-sublist {
        display: none;
        margin-top: 15px;
        &--shown {
          display: block;
        }
        &__link {
          text-decoration: none;

          color: $grey;
        }
      }
    }
    &-contacts {
      @include flex(110px);
      &-links {
        display: none;
        &--mobile {
          display: none;
        }
      }
      &__button {
        display: block;
      }
      &__value,
      &__link {
        display: none;
      }
      &__link--mobile {
        display: block;
        min-width: 40px;
        height: 40px;
        margin-right: 20px;
        border-radius: 50%;

        background: $gold url(../img/svg/call.svg) no-repeat center;
        &:first-child {
          background-image: url(../img/svg/login.svg);
        }
      }
    }
    &-form__item {
      height: 70px;
    }
    &-social {
      padding: 25px 0 30px;
      &-links {
        @include flex-container();
        .header-social__link {
          display: block;
          width: 50px;
          height: 50px;
          margin-right: 35px;
          border-radius: 50%;

          background: $gold url(../img/svg/wa-white.svg) no-repeat center;
          &--facebook {
            background-image: url(../img/svg/facebook-white.svg);
          }
          &--inst {
            background-image: url(../img/svg/inst-white.svg);
          }
        }
      }
    }
  }
}
