.restaurants {
  @media only screen and (max-width: 1200px) {
    &-row {
      justify-content: space-between;
      align-items: center;
    }
    &-slider-container {
      @include flex(62.5%);
      margin-right: 0;
    }
    &-features {
      margin: 0;
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 32px 0 60px;
    &-list {
      margin: 21px 0 29px;
      overflow-x: scroll;
      &__item {
        min-width: max-content;
        padding: 0 15px 19px;
      }
    }
    &__title {
      @include font(20px, 20px);
    }
    &-row {
      flex-wrap: wrap;
    }
    &-slider {
      &-container {
        position: relative;

        @include flex(100vw);
        max-height: 65vw;
        margin-left: -16px;
        margin-top: 21px;
      }
      &-image {
        max-height: 65vw;
      }
      &-controls {
        position: absolute;
        top: 15px;
        right: 17px;

        margin: 0;

        background: $grey-lighter-2;
        &-col {
          &:first-child {
            @include flex(auto);
          }
          &:last-child {
            display: none;
          }
        }
      }
      &__icon {
        display: none;
      }
      &-counter {
        margin: 0;
        padding: 5px 10px;

        @include font(10px, 10px);
      }
      &-image {
        max-width: 100%;
        height: auto;
        margin-top: 0;
      }
    }
    &-features {
      @include flex(100%);
      margin-top: 15px;
      &-row {
        display: flex;
        overflow-x: scroll;
      }
    }
    &-feature {
      min-width: max-content;
      margin-right: 40px;
      &__name {
        margin-bottom: 3px;
      }
      &__value {
        @include font(20px, 20px);
      }
    }
    &-buttons {
      margin-top: 20px;
    }
  }
}
