.contacts {
  @include flex(100%);
  padding: 92px 0 75px;
  &-row {
    @include flex-container();
  }
  &-col {
    @include flex(50%);
    &:last-child {
      margin-top: 43px;
    }
  }
  &-map {
    margin: 44px 0 12px;
    &__link {
      display: none;
    }
    &__item {
      width: 540px;
      height: 380px;
    }
  }
  .ymaps-layers-pane {
    filter: grayscale(1) brightness(90%);
  }
  &-social {
    @include flex-container(row, nowrap, center);

    @include font(30px, 40px);
    font-weight: 300;
    &-links {
      @include flex-container();
      margin-left: 30px;
      .contacts-social__link {
        width: 20px;
        height: 20px;
        margin-right: 20px;

        background: url(../img/svg/wa.svg) no-repeat center;
        &--facebook {
          width: 10px;

          background-image: url(../img/svg/facebook.svg);
        }
        &--inst {
          background-image: url(../img/svg/inst.svg);
        }
      }
    }
  }
  &-info {
    padding: 12px 0 53px;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    &__title {
      margin-bottom: 5px;

      @include font(30px, 40px);
      font-weight: 300;
    }
    &__item {
      margin-top: 17px;
      @include flex(50%);
      &--big {
        @include flex(90%);
      }
    }
    &-row {
      @include flex-container();
    }
    &__name {
      color: $grey-lightest;
    }
    &__value {
      @include font(20px, 30px);
      font-weight: 300;
      text-decoration: none;

      color: #fff;
      &--big {
        max-width: 600px;
        width: 600px;
      }
    }
    &__email {
      @include font(20px, 20px);
      font-weight: 300;
      text-decoration: none;

      color: $gold;
    }
  }
}

@import "media";
