.features {
  @include flex(100%);
  margin-top: 98px;
  .section-title--features {
    display: none;
  }
  &-list {
    @include flex-container(row, nowrap, center, space-between);
    padding: 35px 50px;
    border: 2px solid $gold;
    &__item {
      display: flex;
    }
  }
  &__text {
    max-width: 115px;
  }
  &__icon {
    margin-right: 20px;
    width: 40px;
    height: 40px;

    background: url(../img/svg/parking.svg) no-repeat center;
    &--wifi {
      width: 32px;

      background-image: url(../img/svg/wifi.svg);
    }
    &--room-service {
      background-image: url(../img/svg/room-service.svg);
    }
    &--camera {
      background-image: url(../img/svg/camera.svg);
    }
    &--service {
      width: 32px;

      background-image: url(../img/svg/service.svg);
    }
  }
}

@import "media";
