.services {
  @include flex(100%);
  padding: 124px 0 115px;
  margin-bottom: 75px;
  .tns {
    &-nav + button,
    &-controls {
      display: none;
    }
    &-nav {
      & > button {
        display: none;
      }
    }
  }
  .tns-ovh {
    overflow: visible;
  }
  &-list {
    @include flex-container(row, nowrap, center, space-between);
    max-width: 395px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    &__item {
      padding: 0 20px 22px;

      @include font(15px, 30px);
      text-decoration: none;

      color: #fff;
      &--active {
        border-bottom: 2px solid $gold;

        color: $gold;
      }
    }
  }
  &-preview {
    &-list {
      margin-left: calc((100vw - 1200px) / 2);
      &__item {
        @include flex-container(row, nowrap, normal, space-between);
        display: none;
        &--active {
          display: flex;
        }
      }
    }
    &-slider {
      &-container {
        min-width: 1300px;
        margin-left: 100px;
        overflow: hidden;
      }
      &-image {
        min-width: 650px;
        width: 650px;
        height: 415px;
        overflow: hidden;
      }
      .services-preview-slider-image.tns-slide-active
        .services-preview-slider__img {
        opacity: 1;
      }
      &__img {
        @include img();
        opacity: 0.3;
      }
      &-controls {
        max-width: 660px;
        margin-top: 6px;
        @include flex-container(row, nowrap, normal, space-between);
        &-content {
          @include flex(150px);
          @include flex-container(row, nowrap, center, space-between);
        }
      }
      &-counter {
        margin-top: 13px;
      }
      &__full,
      &__next,
      &__prev {
        width: 48px;
        height: 48px;
        border-radius: 50%;

        cursor: pointer;
        outline: none;
        background: url(../img/svg/arrow.svg) no-repeat center;
        &:hover {
          background-color: rgba(255, 255, 255, 0.1);
        }
      }
      &__prev {
        transform: rotate(180deg);
      }
      &__full {
        background: url(../img/svg/fullscreen.svg) no-repeat center;
      }
    }
  }
  &-info {
    @include flex(450px);
    &__description {
      max-width: 87%;
      margin-bottom: 48px;
    }
    &-features {
      max-width: 85%;
      @include flex-container(row, nowrap, center, space-between);
      margin-bottom: 45px;
    }
    &-feature {
      @include flex-container(row, nowrap, center);
      &__icon {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-right: 10px;

        background: url(../img/svg/area.svg) no-repeat center;
        &--chair {
          background-image: url(../img/svg/chair.svg);
        }
        &--note {
          background-image: url(../img/svg/note.svg);
        }
      }
      &__text {
        line-height: 15px;
      }
    }
    &__buttons {
      @include flex-container(row, nowrap, center);
      & .btn:first-child {
        margin-right: 40px;
      }
    }
  }
  .section-title--services-info {
    margin-top: 52px;
    margin-bottom: 22px;

    color: $gold;
  }
}

@import "media";
