.footer {
  @include flex(100%);

  background: $grey-darker;
  &-top {
    &-row {
      @include flex-container(row, wrap, normal, space-between);
      padding: 60px 0 35px;
    }
    &-col {
      &:first-child {
        @include flex(16.66%);
      }
      &:last-child {
        @include flex(75%);
      }
    }
    &--mobile {
      display: none;
    }
  }
  &-logo {
    margin-bottom: 70px;
    &-image {
      max-width: 138px;
      max-height: 104px;
      overflow: hidden;
    }
    &__img {
      @include img();
    }
  }
  &-stars {
    margin-bottom: 40px;
    &__icon {
      width: 92px;
      height: 20px;
      margin-bottom: 9px;

      background: url(../img/svg/stars.svg) no-repeat center;
      background-size: 100%;
    }
  }
  &-social {
    &-links {
      @include flex-container();
      .footer-social__link {
        width: 20px;
        height: 20px;
        margin-right: 20px;

        background: url(../img/svg/wa.svg) no-repeat center;
        &--facebook {
          width: 10px;

          background-image: url(../img/svg/facebook.svg);
        }
        &--inst {
          background-image: url(../img/svg/inst.svg);
        }
      }
    }
  }
  &-nav {
    @include flex-container(row, wrap, normal, space-between);
    &:first-child {
      &:after {
        content: "";
        display: block;
        width: 100%;
        height: 1px;
        margin: 45px 0 -20px;

        background: #fff;
        opacity: 0.2;
      }
    }
    &__item {
      //@include flex(20%);
      &--restaurants {
        padding-right: 40px;
      }
      &--info {
        margin-top: 57px;
      }
    }
    &__title {
      margin-bottom: 20px;

      @include font(20px, 24px);
      text-decoration: none;

      color: #fff;
      &:hover {
        color: $gold;
      }
    }
    &-list__item {
      margin-bottom: 10px;
    }
    &__link {
      text-decoration: none;

      color: $grey;
      &:hover {
        color: $gold;
      }
    }
  }
  &-contacts {
    @include flex(50%);
    padding-right: 40px;
    &-list {
      @include flex-container(row, wrap, normal, space-between);
      &__item {
        @include flex(45%);
        margin-top: 57px;
      }
    }
    &__title {
      margin-bottom: 22px;

      @include font(20px, 20px);
    }
    &-tel__link,
    &-email__link {
      color: $grey;
    }
    &-tel {
      margin-bottom: 10px;
      &__link {
        text-decoration: none;
      }
    }
  }
  &-bottom {
    @include flex-container(row, wrap, normal, flex-end);
    padding: 20px 0;
    &-row {
      @include flex(75%);
    }
    &__copyright {
      color: $gold;
    }
  }
}

@import "media";
