.rooms {
  @include flex(100%);
  padding: 125px 0 115px;

  background: $grey-darker;
  .section-title--rooms {
    display: none;
  }
  &-row {
    @include flex-container(row, nowrap, normal, space-between);
  }
  &-list {
    @include flex-container(column);
    flex-basis: 210px;
    margin-top: 35px;
    border-left: 1px solid rgba(255, 255, 255, 0.1);
    &__item {
      display: block;
      padding-left: 38px;
      margin-bottom: 20px;

      text-decoration: none;
      &--active {
        border-left: 2px solid $gold;
        .rooms__title {
          color: $gold;
        }
      }
    }
  }
  &__title {
    color: #fff;
  }
  &__price {
    color: $grey-lightest;
  }
  &-preview {
    &-list {
      @include flex(77%);
      &__item {
        display: none;
        &--active {
          display: block;
        }
        &-inner {
          @include flex-container();
          justify-content: flex-end;
        }
      }
    }
    &-info {
      @include flex(33%);
      margin-top: 15px;
      margin-right: 17px;
      &--mobile {
        display: none;
      }
      &-detail {
        display: none;
      }
    }
    &__price {
      @include font(14px, 15px);
      margin-bottom: 10px;
      &--big {
        @include font(25px, 40px);
      }
    }
    &__title {
      display: block;
      margin-bottom: 38px;

      @include font(40px, 40px);
      font-weight: 300;
      text-decoration: 1px #fff solid;
      text-decoration-line: underline;
      text-decoration-style: solid;
      text-decoration-color: #fff;
      text-decoration-thickness: 1px;

      color: #fff;
    }
    &-features {
      @include flex-container(row, wrap, center, flex-start);
      align-items: center;
      margin-bottom: 105px;
    }
    &-feature {
      @include flex(50%);
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      &__icon {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-right: 10px;

        background: url(../img/svg/area.svg) no-repeat center;
        &--bed {
          background-image: url(../img/svg/bed.svg);
        }
        &--bath {
          background-image: url(../img/svg/bath.svg);
        }
        &--table {
          background-image: url(../img/svg/table.svg);
        }
        &--drink {
          background-image: url(../img/svg/drink.svg);
        }
        &--armchair {
          background-image: url(../img/svg/armchair.svg);
        }
      }
      &__text {
        line-height: 15px;
      }
    }
    &-slider {
      .tns {
        &-nav + button,
        &-controls {
          display: none;
        }
        &-nav {
          & > button {
            display: none;
          }
        }
      }
      &-image {
        max-width: 100%;
        max-height: 450px;
        overflow: hidden;
        margin: 0 auto;
      }
      &__img {
        @include img();
      }
      &-controls {
        margin-top: 6px;
        @include flex-container(row, nowrap, normal, space-between);
        &-content {
          @include flex(150px);
          @include flex-container(row, nowrap, center, space-between);
        }
      }
      &-counter {
        margin-top: 13px;
      }
      &__full,
      &__next,
      &__prev {
        width: 48px;
        height: 48px;
        border-radius: 50%;

        cursor: pointer;
        outline: none;
        background: url(../img/svg/arrow.svg) no-repeat center;
        &:hover {
          background-color: rgba(255, 255, 255, 0.1);
        }
      }
      &__prev {
        transform: rotate(180deg);
      }
      &__full {
        background: url(../img/svg/fullscreen.svg) no-repeat center;
      }
    }
  }
}

@import "media";
