h1,
h2,
h3,
h4,
h5,
h6,
ul,
li {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
.container {
  @include container();
  @include xs(max-width, 480px);
  @include sm(max-width, 768px);
  @include md(max-width, 1024px);
  @include lg(max-width, 1280px);
  //@include xl(max-width, 1440px);
  padding: 0 40px;
}
html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  outline: none !important;
}
img {
  max-width: 100%;
}
.body {
  position: relative;

  @include flex-container(column, wrap);
  max-width: 100%;
  min-height: 1000px;
  margin: 0 auto;

  font-family: "Lato", "Open Sans", "Arial", sans-serif;
  @include font(15px, 20px);
  font-weight: 400;

  color: #fff;
  background-color: $grey-lighter;
  .main {
    @include flex(100%, 1, 0);
    margin-top: 110px;
    &--top {
      margin-top: 80px;
    }
  }
}
body section {
  &:nth-child(2n) {
    background: $grey-darker;
  }
}
.section-title {
  font-family: "Lato", "Open Sans", "Arial", sans-serif;
  font-weight: 300;
  @include font(60px, 60px);

  color: #fff;
}
.section-title--gold {
  color: $gold;
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  max-width: 100%;
  margin: 0;
  padding: 18px 40px;
  border: none;

  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  user-select: none;
  @include font(15px, 20px);
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;

  color: #fff;
  background: $gold;
  cursor: pointer;
  &--transparent {
    padding: 16px 38px;
    border: 1px solid $gold;

    color: $gold;

    background-color: transparent;
  }
  &-icon {
    width: 24px;
    height: 24px;
    margin-right: 5px;
    background: top url(../img/svg/dish.svg) no-repeat;
    background-size: contain;
  }
}
.form {
  &-group {
    width: 300px;
    max-width: 300px;
    /*&:nth-last-child(1) {
      margin-top: 18px;
    }*/
  }
  &__item {
    width: 300px;
    max-width: 300px;
    margin-bottom: 22px;
    border: none;
    border-bottom: 1px solid $grey;
    padding-bottom: 6px;

    background: transparent;

    color: #fff;

    &::placeholder {
      color: $grey;

      @include font(14px, 20px);
    }
    &:focus {
      outline: none;
    }
    &:focus,
    &:not(:placeholder-shown) {
      + .label-text {
        position: relative;
        bottom: 72px;

        @include font(12px, 15px);
      }
    }
    &:invalid:not(:placeholder-shown) + .form--error {
      display: block;
    }
    &:invalid:not(:placeholder-shown) {
      border-bottom: 1px solid rgb(134, 19, 19) !important;
    }
    &--textarea {
      position: relative;

      width: 300px;
      min-width: 300px;
      max-width: 300px;
      height: 85px;
      min-height: 85px;
      max-height: 200px;

      text-align: left;
      line-height: 30px;
      & ~ .label-text {
        bottom: 114px;
      }
      &:focus,
      &:not(:placeholder-shown) {
        & + .label-text {
          bottom: 132px;
        }
      }
    }
  }
}
.label {
  position: relative;

  display: block;
  width: 300px;
  max-width: 300px;

  @include font(14px, 20px);

  color: $grey;
  &-text {
    position: relative;
    bottom: 50px;

    transition: bottom 0.2s;
  }
  &--textarea {
    position: relative;
    &:before,
    &:after {
      position: absolute;
      top: 25px;

      content: "";
      display: block;
      width: 300px;
      height: 1px;

      background: $grey;
    }
    &:after {
      top: 54px;
    }
  }
}

@import "media";
