@mixin flex-container(
  $f-direction: row,
  $f-wrap: nowrap,
  $align-items: normal,
  $justify-content: normal
) {
  display: flex;
  flex-direction: $f-direction;
  flex-wrap: $f-wrap;
  align-items: $align-items;
  justify-content: $justify-content;
}
@mixin flex($f-basis, $f-grow: 0, $f-shrink: 0) {
  flex: $f-grow $f-shrink $f-basis;
  width: $f-basis;
  max-width: $f-basis;
}
@mixin img($obj-fit: cover) {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@mixin font($font-size, $line-height) {
  font-size: $font-size;
  line-height: $line-height;
}