.contacts {
  @media only screen and (max-width: 1200px) {
    &-col {
      @include flex(45%);
      &:last-child {
        @include flex(55%);
      }
    }
    &-map__item {
      max-width: 90%;
      max-height: 300px;
    }
  }
  @media only screen and (max-width: 950px) {
    &-row {
      flex-wrap: wrap;
    }
    &-col {
      @include flex(100%);
      &:last-child {
        @include flex(100%);
      }
    }
    &-map__item {
      max-width: 540px;
      max-height: 380px;
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 33px 0 79px;
    .section-title--contacts {
      @include flex-container(row, nowrap, center, space-between);
    }
    &-row {
      flex-wrap: wrap;
    }
    &-col {
      @include flex(100%);
      &:last-child {
        margin-top: 22px;
      }
    }
    &-map {
      margin: 0;
      &__link {
        display: block;

        @include font(15px, 20px);
        font-weight: 300;
        text-decoration: underline;

        color: $gold;
      }
      &__item {
        display: none;
        max-width: 100%;
        max-height: 300px;
        margin-top: 20px;
      }
      &--shown {
        .contacts-map__item {
          display: block;
        }
      }
    }
    &-social {
      display: none;
    }
    &-info {
      padding: 16px 0 17px;
      &:last-child {
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
      }
      &-row {
        margin-top: 20px;
      }
      &__item {
        margin-top: 20px;
        @include flex(100%);
        &:first-child {
          margin-top: 0;
        }
        &:last-child {
          margin-bottom: 23px;
        }
      }
      &__title {
        position: relative;

        @include flex-container(row, nowrap, center, space-between);
        margin: 0;

        @include font(20px, 40px);
        font-weight: 300;
        &--shown {
          padding-bottom: 11px;
          .contacts-info__icon {
            transform: rotate(180deg);
          }
        }
      }
      &__icon {
        display: inline-block;
        width: 10px;
        height: 5px;
        margin-right: 5px;

        background: url(../img/svg/arrow-down-gold.svg) no-repeat center;
      }
      &-list {
        display: none;
        &--shown {
          display: block;
        }
      }
      &__name {
        margin-bottom: 5px;
      }
      &__value {
        @include font(15px, 20px);
        &--big {
          max-width: 100%;
        }
        &-container {
          display: inline;
        }
      }
      &__email {
        @include font(15px, 20px);
        text-decoration: underline;
      }
      &-row {
        flex-wrap: wrap;
      }
    }
  }
}
