.conference {
  @include flex(100%);
  padding: 92px 0 120px;
  &-list {
    @include flex-container(row, nowrap, center);
    margin: 57px 0 48px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    &__item {
      padding: 0 40px 24px;

      @include font(15px, 30px);
      text-decoration: none;

      color: #fff;
      &--active {
        border-bottom: 2px solid $gold;

        color: $gold;
      }
    }
  }
  &-preview {
    &-list {
      &__item {
        display: none;
        &--active {
          display: block;
        }
      }
    }
  }
  &-row {
    @include flex-container();
    .tns {
      &-nav + button,
      &-controls {
        display: none;
      }
      &-nav {
        & > button {
          display: none;
        }
      }
    }
  }
  &__title {
    @include font(40px, 40px);
    font-weight: 300;
  }
  &-slider {
    &-row {
      margin: 0 -15px;
      @include flex-container(row, wrap, normal);
      max-width: 780px;
      transform: none !important;
      .conference-slider-image {
        @include flex(156px);
        max-height: 79px;
        padding: 0 15px;
      }
      .conference-slider__img {
        opacity: 0.8;
      }
      .conference-slider-container {
        padding: 0 15px;
      }
    }
    &-container {
      @include flex(750px);
      margin-right: 150px;
    }
    &-image {
      max-width: 750px;
      max-height: 405px;
      margin-top: 30px;
      overflow: hidden;
    }
    &__img {
      @include img();
    }
    &-controls {
      margin: 6px 0 0;
      @include flex-container(row, nowrap, normal, space-between);
      &-col {
        @include flex-container(row, nowrap, center);
        &:first-child {
          @include flex(150px);
        }
        &:last-child {
          @include flex(150px);
        }
      }
    }
    &-counter {
      margin-right: 20px;
    }
    &__icon,
    &__next,
    &__prev,
    &__full {
      width: 48px;
      height: 48px;
      border-radius: 50%;

      cursor: pointer;
      outline: none;
      background: url(../img/svg/arrow.svg) no-repeat center;
      &:hover {
        background-color: rgba(255, 255, 255, 0.1);
      }
    }
    &__prev {
      transform: rotate(180deg);
    }
    &__full {
      background: url(../img/svg/fullscreen.svg) no-repeat center;
    }
    &__icon {
      background-image: url(../img/svg/carousel.svg);
      &--active {
        background-image: url(../img/svg/carousel-gold.svg);
        &.conference-slider__icon--module {
          background-image: url(../img/svg/module-gold.svg);
        }
      }
      &--module {
        background-image: url(../img/svg/module.svg);
      }
    }
  }
  &-features {
    margin-top: 55px;
  }
  &-feature {
    margin-bottom: 13px;
    &__name {
      margin-bottom: -6px;

      color: $grey-lightest;
    }
    &__value {
      @include font(30px, 40px);
      font-weight: 300;
    }
  }
  &-button {
    margin-top: 35px;
  }
}

@import "media";
