.offers {
  @include flex(100%);
  padding: 112px 0 116px;
  &-top {
    margin-bottom: 53px;
    @include flex-container(row, nowrap, center, space-between);
  }
  .tns {
    &-nav + button,
    &-controls {
      display: none;
    }
    &-nav {
      & > button {
        display: none;
      }
    }
  }
  .tns-ovh {
    overflow: visible;
  }
  &-slider {
    //@include flex-container(row, nowrap, normal, space-between);
    &-container {
      width: 1560px;
      //width: calc(100vw - (100vw - 1200px) / 2);
      margin-left: calc((100vw - 1200px) / 2);
      overflow: hidden;
    }
    &-controls {
      @include flex(120px);
      @include flex-container(row, nowrap, center, space-between);
    }
    &__next,
    &__prev {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      cursor: pointer;
      outline: none;
      background: url(../img/svg/arrow.svg) no-repeat center;
      &:hover {
        background-color: rgba(255, 255, 255, 0.1);
      }
    }
    &__prev {
      margin-right: 20px;

      transform: rotate(180deg);
    }
    &__item {
      width: 450px;
      &:hover .offers-slider-image {
        &:after {
          top: calc(50% - 32px);
          left: calc(50% - 32px);

          width: 64px;
          height: 64px;
          border-radius: 50%;

          background: #fff url(../img/svg/arrow-gold.svg) no-repeat center;
        }
        &:before {
          display: block;
          width: 300px;
          height: 150px;

          background: rgba(161, 143, 87, 0.95);
        }
      }
    }
    &-image {
      position: relative;

      display: block;
      max-width: 100%;
      max-height: 150px;
      height: 150px;
      overflow: hidden;
      margin: 0 auto;
      margin-bottom: 10px;
      &:after,
      &:before {
        content: " ";
        position: absolute;
        z-index: 1;
      }
    }
    &__img {
      position: relative;

      @include img();
    }
    &__title {
      margin-bottom: 9px;

      @include font(25px, 30px);
    }
    &__price {
      color: $gold;
    }
  }
}

@import "media";
