.hotel-services {
  @include flex(100%);
  padding: 67px 0 50px;
  &__text {
    max-width: 600px;
    margin: 17px 0 46px;
  }
  &-outre {
    padding: 0 75px;
  }
  &-row {
    @include flex-container(row, wrap, flex-start, space-between);
    margin: 0 -75px;
  }
  &__item {
    @include flex(33.3%);
    margin-bottom: 75px;
    padding: 0 75px;
  }
  &-image {
    max-width: 100%;
    max-height: 150px;
    height: 150px;
    overflow: hidden;
    margin: 0 auto;
  }
  &__img {
    @include img();
    max-height: 150px;
  }
  &__title {
    margin-top: 17px;

    @include font(25px, 30px);
  }
}

@import "media";
