.conference-booking {
  @include flex(100%);
  padding: 125px 0 120px;
  &-row {
    @include flex-container($justify-content: space-between);
  }
  &-col {
    @include flex(55%);
    &:last-child {
      max-width: 320px;
      margin-top: 3px;
    }
  }
  &-text {
    max-width: 450px;
    margin: 18px 0 190px;

    line-height: 25px;
  }
  &-telnum {
    text-decoration: none;

    color: #fff;
    &:hover {
      color: $gold;
    }
  }
  &-note {
    max-width: 430px;

    color: $grey-lightest;
  }
}

@import "media";
