.services {
  @media only screen and (max-width: 1210px) {
    &-preview {
      &-list {
        max-width: 1280px;
        margin: 0 auto;
        padding: 0 40px;
      }
      &-slider {
        &-container {
          margin-left: 0;
          min-width: 960px;
          max-width: 1000px;
        }
        &-image {
          min-width: 550px;
          width: 550px;
          height: 350px;
        }
        &-controls {
          max-width: 520px;
        }
      }
    }
  }
  @media only screen and (max-width: 1010px) {
    padding: 70px 0;
    .section-title--services-info {
      margin-top: 30px;
    }
    &-info {
      @include flex(330px);
      &__description {
        max-width: 93%;
        margin-bottom: 30px;
      }
      &-features {
        max-width: 93%;
        margin-bottom: 30px;
      }
      &__buttons {
        max-width: 93%;
        flex-direction: column;
        align-items: normal;
        .btn:first-child {
          margin-right: 0;
          margin-bottom: 20px;
        }
      }
    }
    &-preview {
      &-slider {
        &-container {
          margin-top: 30px;
          min-width: 870px;
        }
        &-image {
          min-width: 500px;
          max-width: 500px;
          height: 330px;
        }
        &-controls {
          max-width: 480px;
        }
      }
    }
  }
  @media only screen and (max-width: 830px) {
    margin-bottom: 0;
    padding: 60px 0 0;
    .section-title--services-info {
      margin: 18px 0 11px;
    }
    &-list {
      display: none;
    }
    &-preview {
      &-list {
        @include flex-container(column);
        margin-left: 0;
        padding: 0;
        &__item {
          @include flex-container(column);
          margin-bottom: 80px;
        }
      }
      &-slider {
        position: relative;
        &-container {
          @include flex(100%);
          min-width: 100vw;
          max-width: 100vw;
          margin-left: 0;
        }
        &-image {
          height: 390px;
          max-height: 65vw;
          min-width: 320px;
          max-width: 100vw;
        }
        &__img {
          opacity: 1;
        }
        &-controls {
          position: absolute;
          top: 15px;
          right: 17px;

          margin: 0;

          background: $grey-lighter-2;
          &-content {
            display: none;
          }
        }
        &-counter {
          margin: 0;
          padding: 5px 10px;

          @include font(10px, 10px);
        }
      }
    }
    &-info {
      @include flex(100%);
      order: 1;
      padding: 0 16px;
      &__description {
        max-width: 100%;
        margin-bottom: 20px;
      }
      &-features {
        margin-bottom: 30px;
        max-width: 90%;
      }
      &__buttons {
        flex-direction: column;
        max-width: 100%;
      }
    }
  }
  @media only screen and (max-width: 450px) {
    .services-info-feature {
      flex-direction: column;
      align-items: normal;
      &__icon {
        margin-bottom: 10px;
      }
    }
  }
}
