.conference-info {
  @media only screen and (max-width: 768px) {
    padding: 43px 0 60px;
    &-row {
      flex-wrap: wrap;
    }
    &-col {
      @include flex(100%);
      &:last-child {
        padding: 0;
      }
    }
    &-text {
      margin-bottom: 20px;

      @include font(30px, 30px);
    }
  }
}
