.restaurants-info {
  @media only screen and (max-width: 768px) {
    padding: 43px 0 60px;
    &-row {
      flex-wrap: wrap;
    }
    &-col {
      @include flex(100%);
      &:last-child {
        padding: 0;
      }
    }
    &-text {
      @include font(30px, 30px);
    }
    &-note {
      display: none;
      &--mobile {
        display: block;
        max-width: 100%;
        margin-top: 21px;
        padding: 16px 0 16px 24px;
        border-left: 4px solid $gold;
      }
    }
  }
}
