.book {
  @include flex(100%);
  padding: 120px 0;

  background: #fff !important;
  .section-title--book {
    margin: 7px 0 12px;

    text-align: center;

    color: #222324;
  }
  &-room {
    margin-bottom: 52px;
    display: flex;
    justify-content: center;
    &__link {
      border-bottom: 1px dashed $gold;

      @include font(30px, 40px);
      font-weight: 300;
      text-decoration: none;

      color: $gold;
    }
  }
  &-form {
    max-width: 960px;
    height: 186px;
    margin: -10px auto -85px;
    background: url(../img/svg/booking.svg) no-repeat center;
  }
  &__button {
    display: none;
  }
}

@import "media";
