.salon-price {
  @include flex(100%);
  padding: 127px 0 17px;
  &-list {
    @include flex-container(row, nowrap, center);
    margin: 57px 0 48px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    &__item {
      padding: 0 40px 24px;

      @include font(15px, 30px);
      text-decoration: none;

      color: #fff;
      &--active {
        border-bottom: 2px solid $gold;

        color: $gold;
      }
    }
  }
  &-preview {
    &-list {
      &__item {
        display: none;
        &--active {
          display: block;
        }
      }
    }
    &__title {
      @include font(30px, 40px);
      font-weight: 300;

      color: $gold;
    }
  }
  &-items {
    margin: 32px 0 44px;
  }
  &__item {
    @include flex-container(row, nowrap, center, space-between);
    padding: 14px 0 20px 39px;
    border: 1px solid $grey;
    &:last-child {
      border-top: none;
    }
    &:hover {
      border-color: $gold;

      background: $gold;
      .salon-price {
        &-sum {
          margin-right: 33px;
        }
        &-plus {
          display: block;
          margin-right: 39px;
        }
        &__desc {
          color: #fff;
        }
      }
    }
  }
  &-col {
    &:last-child {
      @include flex-container(row, nowrap, center);
    }
  }
  &__name {
    margin-bottom: 4px;

    @include font(40px, 40px);
    font-weight: 300;
  }
  &__desc {
    color: $grey-lightest;
  }
  &-sum {
    margin-right: 60px;

    @include font(40px, 40px);
    font-weight: 300;
  }
  &-plus {
    display: none;
    min-width: 48px;
    min-height: 48px;
    border-radius: 50%;

    background: #fff url(../img/svg/add-gold.svg) no-repeat center;
  }
  &-note {
    margin: 5px 0 41px;

    color: $grey-lightest;
  }
  &-button {
    @include flex-container($justify-content: center);
    margin-bottom: 108px;
  }
}

@import "media";
