.about {
  @media only screen and (max-width: 1200px) {
    .about-info-image:before {
      display: none;
    }
  }
  @media only screen and (max-width: 1000px) {
    &-text {
      @include flex(100%);
    }
    &-video {
      height: 45vw;
    }
    &-info-col {
      @include flex(50%);
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 32px 0 20px;
    &-text {
      @include flex(100%);

      @include font(20px, 30px);
      &-row {
        margin-bottom: 43px;
      }
    }
    &-video {
      margin-bottom: 43px;
      min-height: 230px;
      &__img {
        min-height: 230px;
      }
    }
    &-info {
      padding-bottom: 20px;
      &-col {
        @include flex(100%);
      }
      &__desc {
        margin: 11px 0 30px;
      }
    }
  }
}
